* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.Rectangle {
  background-image: url(./png/Rectangle.png);
  background-size: cover;
  width: 100%;
  height: 680px;
}

/* .Rectangle > img {
} */
.imgs {
  width: 25px;
  height: 25px;
  margin-right: 10px;
}
.imgs > img {
  margin-right: 30px;
}
.box2 {
  display: flex;
  width: 100%;
  margin: 0 auto;
  background: #fff;
}
.box2 > div {
  width: 25%;
}
.imgItem {
  padding: 35px;
}
.DivImg {
  width: 100%;
}
.DivImg > img {
  width: 100%;
  height: 300px;
}
.Fresh {
  width: 90%;
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
  font-size: 19px;
  line-height: 22px;
  color: #172345;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
}
/* .box2 > div > p {
  margin: 5px 0 10px 0;
} */
h2 {
  color: #5326d4;
  font-size: 35px;
  margin: 0;
}
.StarIcon {
  color: #162345;
}
section {
  height: 74px;
  background: #dee6f3;
  width: 100%;
}
section > div {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 74px;
  padding: 0 20px;
}
section > div > div {
  padding: 17px;
}
section div p {
  margin: 0;
}
.sectionDiv {
  display: flex;
  flex-direction: column;
  text-align: center;
  color: #172345;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  line-height: 39px;
}
.sectionDiv p {
  font-size: 24px;
}
.sectionDiv div > a {
  color: #172345;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  margin: 10px 20px;
}
.productTable {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.imgs1 > div img {
  width: 100%;
}
.imgs1 {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #00aaf8;
  border: 1px solid silver;
}
.imgs > div {
  width: 33.33%;
}
.onClick {
  width: 20%;
}
.imgs1 > div {
  width: 33.33%;
  color: #fff;
  display: flex;
  justify-content: center;
  border-left: 1px solid silver;
  border-right: 1px solid silver;
}
.imgs1 > button {
  color: #fff;
  padding: 10px 20px;
}
.buttonW:hover {
  background: #162345;
}
.imgs1 > img {
  width: 100%;
  max-height: 70%;
}
.MuiDrawer-paper {
  margin-top: 60px;
  width: 28%;
}
.border {
  border: 1px solid silver;
}
.buttonW {
  padding: 10px 20px;
  border-radius: 50px;
}
